
import axios from 'axios'
/*import Xheader from '@/components/comother/rheader'*/
import Vue from 'vue';
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

Vue.component(Popup.name, Popup);
export default {
data(){

return {
contents:{rights:'评分说明',title:''},
list:"",
isYellow:false,
showPop:false
}
},
filters: {
Upper: function (value) {
try {
if(value=== 0)  throw "去处理";
if(value=== 1)  throw "已拒绝";
if(value=== 2)  throw "已通过";
}
catch(err) {
return value=err;
}

},
Upper1: function (value) {
try {
/* if(value===null) throw "";*/
if(value=== 1)  throw "yellowB";
if(value=== 0)  throw "yellowC";
}
catch(err) {
return value=err;
}

},

formatDuring: function (value) {
if(value == "" || value == null || value == undefined){
var value="无";
return value

}else {
Date.prototype.toLocaleString = function(){
let months = this.getMonth()+1;
if(months < 10){
months = '0' + months;
}
let dates = this.getDate();
if(dates < 10){
dates = '0' + dates;
}
let hours = this.getHours();
if(hours < 10){
hours = '0' + hours;
}
let minutes = this.getMinutes();
if(minutes < 10){
minutes = '0' + minutes;
}
return this.getFullYear() +'.'+months+'.'+dates+' '+hours+':'+minutes
}
return new Date(value).toLocaleString();


}}

},
components:{
XHeader,
ViewBox,
Popup,
Flexbox,
FlexboxItem,
},
directives:{
TransferDom
},
methods:{
clickLink(item) {
this.$router.push({
path: '/active/detailPack/',
name: 'detailPack',
params:{studyid:item.studyid,createUserid:item.createUserid,moduleid:this.$route.params.moduleid}
})

},
getModule(){
this.$http.get('pscoredetail/queryById?id='+this.$route.params.moduleid
).then(res =>{
this.contents.title = res.data.projectName+'评分';
}).catch(err =>{
console.log(err)
})
},
getlist(){
axios({
method: 'get',
url: 'pstudy/getPartymemberByDepartmentid',
params: {
departmentid:this.$store.getters.user.departmentid,
activeType:this.$route.params.moduleid
}
}) .then((res)=> {
this.list=res.data;

})
.catch(function (error) {
console.log(error);
});
},
know(){
this.showPop = false
},
datePick(s){
Date.prototype.toLocaleString = function(){
return this.getFullYear() +'.'+ (this.getMonth()+1)+'.'+this.getDate()
};
return new Date(s).toLocaleString();
},
showMenu(){
this.showPop = true;
}
},
mounted(){
this.getlist();
this.getModule();
}
}



